.card {
  background: #fff;
  border-radius: 2px;
  height: 300px;
  margin: 1rem;
  position: relative;
  width: 300px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  margin-top: 1%;
  width: 98%;
  height: 98%;
  -o-object-fit: cover;
     object-fit: cover;
}



